import React from 'react'

const ClientItem = (props) => {
  return (
    <div className=' grid place-items-center h-full w-full py-10' data-aos="fade-up">
    <div className=' text-[#fff]'>
      <img src={props.Clients} alt="clients"/>
    </div>
    </div>
  )
}

export default ClientItem